.popup__container {
    min-width: 400px;
    min-height: 310px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    
}

.popup__title {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ffffff;
    font-size: 1.1vw;
    background-color:#000000;
    width: 81%;
    border: 1px solid #000000;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}
.popup__title>h1 {
    display: flex;
    justify-content: center;
    align-items: center;
}