:root {
    --bg_calendar: #efe4e46f;
    --bg_days: #072740;
    --bg_hours: #832108;
    --bg_class: #6a3660;
    --font_color: #ffffff;
    ---bg-color-cg: #18ff27a7;
    ---bg-color-ia: #dc2c00a5;
    ---bg-color-is: #0ff3ff98;
    ---bg-color-r2: #2900749f;
    ---bg-color-bdd: #00ff9d9c;
}

.calendar__container {
    width: 80%;
    background-color: var(--bg_calendar);
    background-repeat: no-repeat;
    background-size: cover;
    display: grid;
    padding: 1vh;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    grid-template-rows: repeat(14, 1fr);
    grid-template-columns: repeat(13, 1fr);
    grid-template-areas:
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7"
        "n1 n2 n2 n3 n3 n4 n4 n5 n5 n6 n6 n7 n7";
    ;
}

.col {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.item {
    color: var(--font_color);
    background-color: var(--bg_class);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
    font-size: 1.5vw;
    padding: 0.5vh 0;
    margin: 0.5vh;
    width: 90%;

}

.item:hover {
    cursor: context-menu;
}

.hour {
    background-color: var(--bg_hours);
}

#item0 {
    opacity: 0;
}

#day {
    background-color: var(--bg_days);
}

#bg-empty {
    background-color: var(--bg_calendar);
    color: var(--bg_calendar);
    opacity: 0;
}

#clicleable {
    transition: transform 250ms ease-in-out;
}

#clicleable:hover {
    cursor: pointer;
    transform: scale(1.2);
}

.col1 {
    grid-area: n1;
}

.col2 {
    grid-area: n2;
}

.col3 {
    grid-area: n3;
}

.col4 {
    grid-area: n4;
}

.col5 {
    grid-area: n5;
}

.col6 {
    grid-area: n6;
}

.col7 {
    grid-area: n7;
}

.swal2-styled.swal2-confirm {
    width: 80px;
    background-color: red;
    border: none;
}

.swal2-container.swal2-center>.swal2-popup {
    background-color: #072740;
    width: 150px;
    height: 220px;
    font-size: 1.1vw;
}

.CG {
    background-color: var(---bg-color-cg);
}

.IA {
    background-color: var(---bg-color-ia);
}

.R2 {
    background-color: var(---bg-color-r2);
}

.IS {
    background-color: var(---bg-color-is);
}

.BDD {
    background-color: var(---bg-color-bdd);
}